import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { Helmet } from 'react-helmet-async';
import {
  User,
  Mail,
  Phone,
  Calendar,
  MapPin,
  GraduationCap,
  Edit,
  Save,
  X,
  Camera,
  Shield,
  Clock
} from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';
import { toast } from 'react-hot-toast';
import LoadingSpinner from '../components/ui/LoadingSpinner';


const Profile = () => {
  const { user, updateProfile } = useAuth();
  const queryClient = useQueryClient();
  const [isEditing, setIsEditing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  // Fetch user profile data from backend
  const { data: profileApiResponse, isLoading: profileLoading, error: profileError } = useQuery(
    ['user-profile', user?._id],
    () => api.get('/api/auth/me').then(res => res.data),
    {
      enabled: !!user?._id,
      staleTime: 5 * 60 * 1000, // 5 minutes
    }
  );

  // Extract user data from API response
  const profileData = profileApiResponse?.user || user || {};

  // Debug: log what we get from backend and context
  if (process.env.NODE_ENV !== 'production') {
    // eslint-disable-next-line no-console
    console.log('Profile API response:', profileApiResponse);
    // eslint-disable-next-line no-console
    console.log('User from context:', user);
    // eslint-disable-next-line no-console
    console.log('Profile data used:', profileData);
  }

  // Initialize formData with profileData
  const [formData, setFormData] = useState({
    fullName: profileData?.fullName || '',
    email: profileData?.email || '',
    phoneNumber: profileData?.phoneNumber || '',
    department: profileData?.department || '',
    year: profileData?.year || '',
    semester: profileData?.semester || '',
    interests: Array.isArray(profileData?.interests) ? profileData.interests.join(', ') : '',
    bio: profileData?.bio || ''
  });

  // Update formData when profileData changes
  React.useEffect(() => {
    setFormData({
      fullName: profileData?.fullName || '',
      email: profileData?.email || '',
      phoneNumber: profileData?.phoneNumber || '',
      department: profileData?.department || '',
      year: profileData?.year || '',
      semester: profileData?.semester || '',
      interests: Array.isArray(profileData?.interests) ? profileData.interests.join(', ') : '',
      bio: profileData?.bio || ''
    });
  }, [profileData]);

  // Update profile mutation
  const updateProfileMutation = useMutation(
    (data) => api.put('/api/auth/profile', data),
    {
      onSuccess: (response) => {
        updateProfile(response.data);
        queryClient.invalidateQueries(['user-profile', user?._id]);
        toast.success('Profile updated successfully!');
        setIsEditing(false);
      },
      onError: (error) => {
        toast.error(error.response?.data?.message || 'Failed to update profile');
      }
    }
  );

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSave = async () => {
    setIsLoading(true);
    try {
      const dataToSend = {
        ...formData,
        interests: formData.interests ? formData.interests.split(',').map(i => i.trim()) : []
      };
      
      await updateProfileMutation.mutateAsync(dataToSend);
    } catch (error) {
      console.error('Profile update error:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    setFormData({
      fullName: user?.fullName || '',
      email: user?.email || '',
      phoneNumber: user?.phoneNumber || '',
      department: user?.department || '',
      year: user?.year || '',
      semester: user?.semester || '',
      interests: user?.interests?.join(', ') || '',
      bio: user?.bio || ''
    });
    setIsEditing(false);
  };


  if (profileLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </div>
    );
  }

  if (profileError) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-red-500">Error loading profile: {profileError.message}</div>
      </div>
    );
  }

  // Use profileData for all fields
  const currentUser = profileData;

  return (
    <>
      <Helmet>
        <title>Profile - EventSphere</title>
        <meta name="description" content="View and edit your EventSphere profile information." />
      </Helmet>

      <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
        {/* Header */}
        <div className="bg-white dark:bg-gray-800 shadow-sm border-b border-gray-200 dark:border-gray-700">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
                My Profile
              </h1>
              <p className="text-lg text-gray-600 dark:text-gray-400">
                Manage your personal information and preferences
              </p>
            </motion.div>
          </div>
        </div>

        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Profile Picture Section */}
            <div className="lg:col-span-1">
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5, delay: 0.1 }}
                className="bg-white dark:bg-gray-800 rounded-xl shadow-sm border border-gray-200 dark:border-gray-700 p-6 text-center"
              >
                <div className="relative mb-6">
                  {currentUser?.profileImage ? (
                    <img
                      src={currentUser.profileImage}
                      alt={currentUser.fullName}
                      className="w-32 h-32 rounded-full mx-auto object-cover border-4 border-white dark:border-gray-700 shadow-lg"
                    />
                  ) : (
                    <div className="w-32 h-32 rounded-full mx-auto bg-gradient-to-br from-primary-100 to-secondary-100 dark:from-primary-900 dark:to-secondary-900 flex items-center justify-center border-4 border-white dark:border-gray-700 shadow-lg">
                      <User size={48} className="text-primary-600 dark:text-primary-400" />
                    </div>
                  )}
                  
                  <button className="absolute bottom-0 right-0 w-10 h-10 bg-primary-600 rounded-full flex items-center justify-center text-white shadow-lg hover:bg-primary-700 transition-colors">
                    <Camera size={20} />
                  </button>
                </div>

                <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                  {currentUser?.fullName}
                </h2>
                
                <div className="flex items-center justify-center gap-2 mb-4">
                  <span className="px-3 py-1 bg-primary-100 dark:bg-primary-900 text-primary-800 dark:text-primary-200 text-sm font-medium rounded-full">
                    {currentUser?.role?.charAt(0).toUpperCase() + currentUser?.role?.slice(1)}
                  </span>
                </div>

                <div className="text-sm text-gray-600 dark:text-gray-400 space-y-1">
                  <p>Member since {new Date(currentUser?.createdAt || Date.now()).toLocaleDateString()}</p>
                  <p>Last login: {new Date(currentUser?.lastLogin || Date.now()).toLocaleDateString()}</p>
                </div>
              </motion.div>
            </div>

            {/* Profile Information */}
            <div className="lg:col-span-2">
              <motion.div
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5, delay: 0.2 }}
                className="bg-white dark:bg-gray-800 rounded-xl shadow-sm border border-gray-200 dark:border-gray-700 p-6"
              >
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                    Personal Information
                  </h3>
                  <div className="flex items-center gap-2">
                    {isEditing ? (
                      <>
                        <button
                          onClick={handleSave}
                          disabled={isLoading}
                          className="btn-primary flex items-center gap-2 px-4 py-2"
                        >
                          {isLoading ? (
                            <LoadingSpinner size="sm" />
                          ) : (
                            <Save size={16} />
                          )}
                          Save
                        </button>
                        <button
                          onClick={handleCancel}
                          className="btn-outline flex items-center gap-2 px-4 py-2"
                        >
                          <X size={16} />
                          Cancel
                        </button>
                      </>
                    ) : (
                      <button
                        onClick={() => setIsEditing(true)}
                        className="btn-outline flex items-center gap-2 px-4 py-2"
                      >
                        <Edit size={16} />
                        Edit Profile
                      </button>
                    )}
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {/* Full Name */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Full Name
                    </label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="fullName"
                        value={formData.fullName}
                        onChange={handleInputChange}
                        className="input w-full"
                        placeholder="Enter your full name"
                      />
                    ) : (
                      <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <User size={20} className="text-gray-400" />
                        <span className="text-gray-900 dark:text-white">
                          {currentUser?.fullName || 'Not provided'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Email */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Email Address
                    </label>
                    <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <Mail size={20} className="text-gray-400" />
                      <span className="text-gray-900 dark:text-white">
                        {currentUser?.email}
                      </span>
                      <Shield size={16} className="text-green-500" title="Verified" />
                    </div>
                  </div>

                  {/* Phone Number */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Phone Number
                    </label>
                    {isEditing ? (
                      <input
                        type="tel"
                        name="phoneNumber"
                        value={formData.phoneNumber}
                        onChange={handleInputChange}
                        className="input w-full"
                        placeholder="Enter your phone number"
                      />
                    ) : (
                      <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <Phone size={20} className="text-gray-400" />
                        <span className="text-gray-900 dark:text-white">
                          {currentUser?.phoneNumber || 'Not provided'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Date of Birth */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Date of Birth
                    </label>
                    <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <Calendar size={20} className="text-gray-400" />
                      <span className="text-gray-900 dark:text-white">
                        {currentUser?.dateOfBirth 
                          ? new Date(currentUser.dateOfBirth).toLocaleDateString()
                          : 'Not provided'
                        }
                      </span>
                      {currentUser?.age && (
                        <span className="text-sm text-gray-500 dark:text-gray-400">
                          ({currentUser.age} years old)
                        </span>
                      )}
                    </div>
                  </div>

                  {/* Department */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Department
                    </label>
                    {isEditing ? (
                      <select
                        name="department"
                        value={formData.department}
                        onChange={handleInputChange}
                        className="input w-full"
                      >
                        <option value="">Select department</option>
                        <option value="Computer Science">Computer Science</option>
                        <option value="Information Technology">Information Technology</option>
                        <option value="Software Engineering">Software Engineering</option>
                        <option value="Data Science">Data Science</option>
                        <option value="Cybersecurity">Cybersecurity</option>
                        <option value="Artificial Intelligence">Artificial Intelligence</option>
                        <option value="Computer Engineering">Computer Engineering</option>
                      </select>
                    ) : (
                      <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <MapPin size={20} className="text-gray-400" />
                        <span className="text-gray-900 dark:text-white">
                          {currentUser?.department || 'Not provided'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Year */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Year
                    </label>
                    {isEditing ? (
                      <select
                        name="year"
                        value={formData.year}
                        onChange={handleInputChange}
                        className="input w-full"
                      >
                        <option value="">Select year</option>
                        <option value="1st">1st Year</option>
                        <option value="2nd">2nd Year</option>
                        <option value="3rd">3rd Year</option>
                        <option value="4th">4th Year</option>
                        <option value="Masters 1st">Masters 1st Year</option>
                        <option value="Masters 2nd">Masters 2nd Year</option>
                      </select>
                    ) : (
                      <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <GraduationCap size={20} className="text-gray-400" />
                        <span className="text-gray-900 dark:text-white">
                          {currentUser?.year || 'Not provided'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Semester */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Semester
                    </label>
                    {isEditing ? (
                      <select
                        name="semester"
                        value={formData.semester}
                        onChange={handleInputChange}
                        className="input w-full"
                      >
                        <option value="">Select semester</option>
                        <option value="1st">1st Semester</option>
                        <option value="2nd">2nd Semester</option>
                        <option value="3rd">3rd Semester</option>
                        <option value="4th">4th Semester</option>
                        <option value="5th">5th Semester</option>
                        <option value="6th">6th Semester</option>
                        <option value="7th">7th Semester</option>
                        <option value="8th">8th Semester</option>
                      </select>
                    ) : (
                      <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <Clock size={20} className="text-gray-400" />
                        <span className="text-gray-900 dark:text-white">
                          {currentUser?.semester || 'Not provided'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Scholar Number */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Scholar Number
                    </label>
                    <div className="flex items-center gap-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <GraduationCap size={20} className="text-gray-400" />
                      <span className="text-gray-900 dark:text-white">
                        {currentUser?.scholarNumber || 'Not provided'}
                      </span>
                    </div>
                  </div>
                </div>

                {/* Interests */}
                <div className="mt-6">
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Interests
                  </label>
                  {isEditing ? (
                    <textarea
                      name="interests"
                      value={formData.interests}
                      onChange={handleInputChange}
                      className="input w-full"
                      rows={3}
                      placeholder="Enter your interests (comma separated)"
                    />
                  ) : (
                    <div className="p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      {currentUser?.interests && currentUser.interests.length > 0 ? (
                        <div className="flex flex-wrap gap-2">
                          {currentUser.interests.map((interest, index) => (
                            <span
                              key={index}
                              className="px-3 py-1 bg-primary-100 dark:bg-primary-900 text-primary-800 dark:text-primary-200 text-sm rounded-full"
                            >
                              {interest}
                            </span>
                          ))}
                        </div>
                      ) : (
                        <span className="text-gray-500 dark:text-gray-400">No interests specified</span>
                      )}
                    </div>
                  )}
                </div>

                {/* Bio */}
                <div className="mt-6">
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Bio
                  </label>
                  {isEditing ? (
                    <textarea
                      name="bio"
                      value={formData.bio}
                      onChange={handleInputChange}
                      className="input w-full"
                      rows={4}
                      placeholder="Tell us about yourself..."
                    />
                  ) : (
                    <div className="p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <span className="text-gray-900 dark:text-white">
                        {currentUser?.bio || 'No bio provided'}
                      </span>
                    </div>
                  )}
                </div>
              </motion.div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
