import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { Helmet } from 'react-helmet-async';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';
import { toast } from 'react-hot-toast';
import LoadingSpinner from '../components/ui/LoadingSpinner';
import EventCard from '../components/events/EventCard';

const EventDetail = () => {
  const { eventId } = useParams();
  const navigate = useNavigate();
  const { user, isAuthenticated } = useAuth();
  const queryClient = useQueryClient();
  
  const [isLiked, setIsLiked] = useState(false);
  const [showShareMenu, setShowShareMenu] = useState(false);
  const [registrationStatus, setRegistrationStatus] = useState(null);

  // Like/Unlike mutation
  const likeMutation = useMutation(
    () => api.post(`/api/events/${eventId}/like`),
    {
      onSuccess: () => {
        setIsLiked(!isLiked);
        queryClient.invalidateQueries(['event', eventId]);
        toast.success(isLiked ? 'Removed from favorites' : 'Added to favorites');
      },
      onError: (error) => {
        toast.error(error.response?.data?.message || 'Failed to update like');
      }
    }
  );

  // Registration mutation
  const registrationMutation = useMutation(
    (data) => api.post('/api/registrations', data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['userRegistration', eventId]);
        queryClient.invalidateQueries(['event', eventId]);
        toast.success('Successfully registered for the event!');
      },
      onError: (error) => {
        toast.error(error.response?.data?.message || 'Failed to register for event');
      }
    }
  );

  const handleLike = () => {
    if (!isAuthenticated) {
      toast.error('Please login to like events');
      return;
    }
    likeMutation.mutate();
  };

  const handleRegister = () => {
    if (!isAuthenticated) {
      navigate('/login', { state: { from: `/events/${eventId}` } });
      return;
    }

    const registrationData = {
      event: eventId,
      participant: user._id,
      registrationType: 'participant'
    };

    registrationMutation.mutate(registrationData);
  };

  // Fetch event details
  const { data: event, isLoading, error } = useQuery(
    ['event', eventId],
    () => api.get(`/api/events/${eventId}`).then(res => res.data.event),
    { staleTime: 5 * 60 * 1000 }
  );

  // Fetch related events
  const { data: relatedEvents } = useQuery(
    ['relatedEvents', eventId],
    () => api.get(`/api/events/${eventId}/related`).then(res => res.data.relatedEvents),
    { enabled: !!event, staleTime: 10 * 60 * 1000 }
  );

  // Check if user is registered
  const { data: userRegistration } = useQuery(
    ['userRegistration', eventId],
    () => api.get(`/api/registrations/my?event=${eventId}`).then(res => res.data),
    { enabled: !!event && isAuthenticated, staleTime: 2 * 60 * 1000 }
  );

  // Check if user liked the event
  useEffect(() => {
    if (event && user) {
      setIsLiked(event.likes?.includes(user._id) || false);
    }
  }, [event, user]);

  // Set registration status
  useEffect(() => {
    if (userRegistration?.registrations?.length > 0) {
      const reg = userRegistration.registrations[0];
      setRegistrationStatus(reg.status);
    }
  }, [userRegistration]);

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
            Event Not Found
          </h2>
          <p className="text-gray-600 dark:text-gray-400 mb-4">
            The event you're looking for doesn't exist or has been removed.
          </p>
          <button
            onClick={() => navigate('/events')}
            className="btn-primary"
          >
            Back to Events
          </button>
        </div>
      </div>
    );
  }

  if (!event) return null;

  return (
    <>
      <Helmet>
        <title>{event.title} - EventSphere</title>
        <meta name="description" content={event.shortDescription} />
      </Helmet>

      <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
        {/* Back Button */}
        <div className="bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
            <button
              onClick={() => navigate('/events')}
              className="flex items-center gap-2 text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-colors"
            >
              ← Back to Events
            </button>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
            {event.title}
          </h1>
          
          {/* Event Meta Info */}
          <div className="flex flex-wrap gap-4 text-sm text-gray-600 dark:text-gray-400 mb-8">
            <span className="flex items-center gap-2">
              📅 {new Date(event.startDate).toLocaleDateString('en-US', {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric'
              })}
            </span>
            <span className="flex items-center gap-2">
              ⏰ {event.startTime} - {event.endTime}
            </span>
            <span className="flex items-center gap-2">
              📍 {event.venue}
            </span>
            <span className="flex items-center gap-2">
              👥 {event.currentParticipants}/{event.maxParticipants} participants
            </span>
            {event.rating && (
              <span className="flex items-center gap-2">
                ⭐ {event.rating.toFixed(1)} ({event.ratingCount || 0} reviews)
              </span>
            )}
          </div>

          {/* Tags */}
          {event.tags && event.tags.length > 0 && (
            <div className="flex flex-wrap gap-2 mb-8">
              {event.tags.map((tag, index) => (
                <span
                  key={index}
                  className="px-3 py-1 bg-primary-100 dark:bg-primary-900 text-primary-800 dark:text-primary-200 text-sm rounded-full"
                >
                  {tag}
                </span>
              ))}
            </div>
          )}
          
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Main Content */}
            <div className="lg:col-span-2 space-y-8">
              {/* Event Description */}
              <div>
                <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                  About This Event
                </h2>
                <p className="text-gray-700 dark:text-gray-300 leading-relaxed">
                  {event.description}
                </p>
              </div>

              {/* Event Objectives */}
              {event.objectives && event.objectives.length > 0 && (
                <div>
                  <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-3">
                    Learning Objectives
                  </h3>
                  <ul className="space-y-2 text-gray-700 dark:text-gray-300">
                    {event.objectives.map((objective, index) => (
                      <li key={index} className="flex items-start gap-2">
                        <span className="text-green-500 mt-1">•</span>
                        <span>{objective}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Related Events */}
              {relatedEvents && relatedEvents.length > 0 && (
                <div className="space-y-4">
                  <h2 className="text-2xl font-semibold text-gray-900 dark:text-white">
                    You May Also Like
                  </h2>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {relatedEvents.slice(0, 4).map((relatedEvent) => (
                      <EventCard key={relatedEvent._id} event={relatedEvent} viewMode="list" />
                    ))}
                  </div>
                </div>
              )}
            </div>

            {/* Right Sidebar */}
            <div className="space-y-6">
              {/* Registration Card */}
              <div className="bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700 sticky top-6">
                <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
                  Event Schedule
                </h3>
                
                <div className="space-y-4">
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                      <span className="text-primary-600 text-sm font-medium">📅</span>
                    </div>
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">
                        {new Date(event.startDate).toLocaleDateString('en-US', {
                          weekday: 'short',
                          month: 'short',
                          day: 'numeric'
                        })}
                      </p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        {new Date(event.startDate).toLocaleDateString('en-US', {
                          year: 'numeric'
                        })}
                      </p>
                    </div>
                  </div>

                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                      <span className="text-primary-600 text-sm font-medium">⏰</span>
                    </div>
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">
                        {event.startTime} - {event.endTime}
                      </p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        Duration: {event.duration}
                      </p>
                    </div>
                  </div>

                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                      <span className="text-primary-600 text-sm font-medium">📍</span>
                    </div>
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">
                        {event.venue}
                      </p>
                    </div>
                  </div>

                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                      <span className="text-primary-600 text-sm font-medium">👥</span>
                    </div>
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">
                        {event.currentParticipants}/{event.maxParticipants}
                      </p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        participants registered
                      </p>
                    </div>
                  </div>
                </div>

                <div className="mt-6 pt-6 border-t border-gray-200 dark:border-gray-700">
                  {!isAuthenticated ? (
                    <button
                      onClick={() => navigate('/login', { state: { from: `/events/${eventId}` } })}
                      className="btn-primary w-full"
                    >
                      Login to Register
                    </button>
                  ) : registrationStatus ? (
                    <div className="text-center">
                      <div className="flex items-center justify-center gap-2 mb-2">
                        <span className="text-green-500 text-xl">✓</span>
                        <span className="font-medium text-gray-900 dark:text-white">
                          {registrationStatus === 'confirmed' ? 'Registered' : 'Registration Pending'}
                        </span>
                      </div>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        {registrationStatus === 'confirmed' 
                          ? 'You\'re all set for this event!'
                          : 'Your registration is being reviewed.'}
                      </p>
                    </div>
                  ) : (
                    <button
                      onClick={handleRegister}
                      disabled={registrationMutation.isLoading}
                      className="btn-primary w-full"
                    >
                      {registrationMutation.isLoading ? (
                        <div className="flex items-center justify-center gap-2">
                          <LoadingSpinner size="sm" />
                          Registering...
                        </div>
                      ) : (
                        'Enroll Now'
                      )}
                    </button>
                  )}
                </div>
              </div>

              {/* Quick Stats */}
              <div className="bg-white dark:bg-gray-800 rounded-xl p-6 shadow-sm border border-gray-200 dark:border-gray-700">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                  Event Statistics
                </h3>
                
                <div className="space-y-3">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-600 dark:text-gray-400">Likes</span>
                    <span className="font-medium text-gray-900 dark:text-white">
                      {event.likeCount || 0}
                    </span>
                  </div>
                  
                  <div className="flex justify-between items-center">
                    <span className="text-gray-600 dark:text-gray-400">Registration Deadline</span>
                    <span className="font-medium text-gray-900 dark:text-white">
                      {new Date(event.registrationDeadline).toLocaleDateString()}
                    </span>
                  </div>
                  
                  {event.rating && (
                    <div className="flex justify-between items-center">
                      <span className="text-gray-600 dark:text-gray-400">Average Rating</span>
                      <div className="flex items-center gap-1">
                        <span className="text-yellow-500">⭐</span>
                        <span className="font-medium text-gray-900 dark:text-white">
                          {event.rating.toFixed(1)}
                        </span>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default EventDetail;
