
import React, { useState } from 'react';
import { CalendarDays, CheckCircle, Bell, Download, Star, Lock } from 'lucide-react';
import { Link } from 'react-router-dom';
import { useQuery } from 'react-query';
import { toast } from 'react-hot-toast';
import api from '../../services/api';

const features = [
  {
    icon: <CalendarDays className="w-7 h-7" />,
    title: 'Browse Events',
    desc: 'View upcoming, ongoing, and past university events.'
  },
  {
    icon: <CheckCircle className="w-7 h-7" />,
    title: 'Register & Check-in',
    desc: 'Register for events and check-in via QR code.'
  },
  {
    icon: <Bell className="w-7 h-7" />,
    title: 'Notifications',
    desc: 'Get reminders and updates for your registered events.'
  },
  {
    icon: <Download className="w-7 h-7" />,
    title: 'Certificates',
    desc: 'Download certificates after event completion.'
  },
  {
    icon: <Star className="w-7 h-7" />,
    title: 'Feedback',
    desc: 'Submit feedback and ratings for events.'
  },
];

const StudentDashboard = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedStatus, setSelectedStatus] = useState('');
  const [page, setPage] = useState(1);

  const params = new URLSearchParams({
    page: String(page),
    limit: '9',
    sortBy: 'startDate',
    sortOrder: 'asc',
    search: searchTerm,
    status: selectedStatus
  });

  const { data: eventsData, isLoading } = useQuery(
    ['student-dashboard-events', params.toString()],
    () => api.get(`/api/events?${params.toString()}`).then(res => res.data),
    { staleTime: 2 * 60 * 1000 }
  );

  const handleRegister = async (eventId) => {
    try {
      await api.post(`/api/events/${eventId}/register`);
      toast.success('Registered successfully');
    } catch (err) {
      toast.error(err.response?.data?.message || 'Registration failed');
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-50 dark:from-slate-900 dark:via-slate-800 dark:to-slate-900">
      {/* Header */}
      <div className="bg-white/80 dark:bg-slate-800/80 backdrop-blur-sm shadow-sm border-b border-slate-200 dark:border-slate-700 sticky top-0 z-10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold bg-gradient-to-r from-slate-900 to-slate-600 dark:from-white dark:to-slate-300 bg-clip-text text-transparent">Student Dashboard</h1>
              <p className="text-slate-600 dark:text-slate-400 mt-1">Welcome back! Here’s a quick overview and shortcuts.</p>
            </div>
            <span className="px-4 py-2 bg-gradient-to-r from-blue-500 to-blue-600 text-white text-sm font-medium rounded-full shadow-lg">Student</span>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Link to="/events" className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm hover:shadow-lg transition block">
            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500 to-blue-600 text-white flex items-center justify-center mb-4">
              <CalendarDays className="w-6 h-6" />
            </div>
            <div className="font-semibold text-slate-900 dark:text-white">Browse Events</div>
            <div className="text-sm text-slate-600 dark:text-slate-400">Find events to join</div>
          </Link>
          <Link to="/my-registrations" className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm hover:shadow-lg transition block">
            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-emerald-500 to-emerald-600 text-white flex items-center justify-center mb-4">
              <CheckCircle className="w-6 h-6" />
            </div>
            <div className="font-semibold text-slate-900 dark:text-white">My Registrations</div>
            <div className="text-sm text-slate-600 dark:text-slate-400">View your registered events</div>
          </Link>
          <Link to="/my-feedback" className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm hover:shadow-lg transition block">
            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-amber-500 to-amber-600 text-white flex items-center justify-center mb-4">
              <Star className="w-6 h-6" />
            </div>
            <div className="font-semibold text-slate-900 dark:text-white">My Feedback</div>
            <div className="text-sm text-slate-600 dark:text-slate-400">Rate and review your events</div>
          </Link>
        </div>

        {/* Feature Highlights */}
        <div className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm mb-8">
          <h2 className="text-lg font-semibold text-slate-900 dark:text-white mb-4">What you can do</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {features.map((f, i) => (
              <div key={i} className="rounded-2xl border border-slate-200 dark:border-slate-700 p-5 hover:shadow-md transition bg-slate-50/50 dark:bg-slate-900/30">
                <div className="w-10 h-10 rounded-lg bg-slate-900/90 dark:bg-white/10 text-white flex items-center justify-center mb-3">
                  {f.icon}
                </div>
                <div className="text-slate-900 dark:text-white font-medium">{f.title}</div>
                <div className="text-sm text-slate-600 dark:text-slate-400">{f.desc}</div>
              </div>
            ))}
          </div>
        </div>

        {/* Events with Register */}
        <div className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm mb-8">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-slate-900 dark:text-white">Events</h2>
            <Link to="/events" className="text-sm text-blue-600 dark:text-blue-400 hover:underline">View all</Link>
          </div>
          <div className="flex flex-col sm:flex-row gap-3 mb-4">
            <div className="flex-1">
              <input
                type="text"
                value={searchTerm}
                onChange={(e)=>{ setSearchTerm(e.target.value); setPage(1); }}
                placeholder="Search events..."
                className="w-full px-3 py-2 rounded-lg border border-slate-300 dark:border-slate-600 bg-white dark:bg-slate-700 text-slate-900 dark:text-white"
              />
            </div>
            <select
              value={selectedStatus}
              onChange={(e)=>{ setSelectedStatus(e.target.value); setPage(1); }}
              className="px-3 py-2 rounded-lg border border-slate-300 dark:border-slate-600 bg-white dark:bg-slate-700 text-slate-900 dark:text-white"
            >
              <option value="">All Status</option>
              <option value="upcoming">Upcoming</option>
              <option value="ongoing">Ongoing</option>
              <option value="completed">Completed</option>
              <option value="cancelled">Cancelled</option>
            </select>
          </div>
          {isLoading ? (
            <div className="py-8 text-center text-slate-600 dark:text-slate-400">Loading events...</div>
          ) : (eventsData?.events?.length ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {eventsData.events.map((ev) => (
                <div key={ev._id} className="rounded-2xl border border-slate-200 dark:border-slate-700 p-5 hover:shadow-md transition bg-white dark:bg-slate-800">
                  <div className="flex items-start justify-between mb-2">
                    <div className="font-semibold text-slate-900 dark:text-white">{ev.title}</div>
                    <span className="text-xs px-2 py-1 rounded-full bg-blue-50 text-blue-700 dark:bg-blue-900/20 dark:text-blue-300">{ev.category}</span>
                  </div>
                  <div className="text-sm text-slate-600 dark:text-slate-400 mb-3">{new Date(ev.startDate).toLocaleDateString()} • {ev.venue}</div>
                  <div className="flex items-center justify-between">
                    <Link to={`/events/${ev._id}`} className="text-sm text-slate-700 dark:text-slate-300 hover:underline">Details</Link>
                    <button onClick={() => handleRegister(ev._id)} className="px-3 py-1.5 rounded-lg bg-gradient-to-r from-blue-500 to-blue-600 text-white text-sm shadow hover:shadow-lg">Register</button>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="py-8 text-center text-slate-600 dark:text-slate-400">No events found</div>
          ))}
          <div className="mt-6 flex items-center justify-center gap-2">
            <button onClick={()=> setPage(p=> Math.max(1, p-1))} disabled={(eventsData?.pagination?.currentPage||1) === 1} className="px-3 py-2 rounded-lg border border-slate-300 dark:border-slate-600 disabled:opacity-50">Prev</button>
            <span className="text-sm text-slate-600 dark:text-slate-400">Page {eventsData?.pagination?.currentPage || page} of {eventsData?.pagination?.totalPages || 1}</span>
            <button onClick={()=> setPage(p=> Math.min((eventsData?.pagination?.totalPages||1), p+1))} disabled={(eventsData?.pagination?.currentPage||1) === (eventsData?.pagination?.totalPages||1)} className="px-3 py-2 rounded-lg border border-slate-300 dark:border-slate-600 disabled:opacity-50">Next</button>
          </div>
        </div>

        {/* Restrictions */}
        <div className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700 shadow-sm">
          <div className="flex items-center gap-2 text-rose-600 dark:text-rose-400 font-semibold">
            <Lock className="w-5 h-5" />
            <span>Restricted Actions</span>
          </div>
          <ul className="mt-3 text-slate-600 dark:text-slate-400 text-sm list-disc list-inside">
            <li>Cannot create, edit, or cancel events</li>
            <li>Cannot create accounts</li>
            <li>Cannot approve or reject events</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default StudentDashboard;
