import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { 
  Calendar, 
  Clock, 
  MapPin, 
  Users, 
  Heart, 
  Eye,
  Star
} from 'lucide-react';
import { format } from 'date-fns';
import { useAuth } from '../../contexts/AuthContext';
import { useMutation, useQueryClient } from 'react-query';
import api from '../../services/api';
import toast from 'react-hot-toast';

const EventCard = ({ event }) => {
  // Debug: Log event object and _id
  console.log('EventCard render:', event, 'event._id:', event && event._id);
  const [isLiked, setIsLiked] = useState(false);
  const { isAuthenticated, user } = useAuth();
  const queryClient = useQueryClient();

  // Set initial like status based on user and event likes
  React.useEffect(() => {
    if (event && user && event.likes) {
      setIsLiked(event.likes.includes(user._id));
    }
  }, [event, user]);

  // Like/Unlike mutation
  const likeMutation = useMutation(
    async () => {
      const response = await api.post(`/api/events/${event._id}/like`);
      return response.data;
    },
    {
      onSuccess: (data) => {
        setIsLiked(data.isLiked);
        // Invalidate and refetch events
        queryClient.invalidateQueries(['featuredEvents']);
        queryClient.invalidateQueries(['events']);
      },
      onError: (error) => {
        toast.error('Failed to update like status');
      },
    }
  );

  const handleLike = () => {
    if (!isAuthenticated) {
      toast.error('Please login to like events');
      return;
    }
    likeMutation.mutate();
  };

  const formatDate = (dateString) => {
    if (!dateString) {
      console.warn('EventCard: Missing date value:', dateString, event);
      return 'No Date';
    }
    const dateObj = new Date(dateString);
    if (isNaN(dateObj.getTime())) {
      console.warn('EventCard: Invalid date value:', dateString, event);
      return 'Invalid Date';
    }
    return format(dateObj, 'MMM dd, yyyy');
  };

  const formatTime = (timeString) => {
    if (!timeString) return '';
    return timeString;
  };

  const getEventStatus = () => {
    const now = new Date();
    const eventDate = new Date(event.startDate);
    
    if (eventDate < now) {
      return { text: 'Past', color: 'bg-gray-500' };
    } else if (eventDate.getTime() - now.getTime() < 24 * 60 * 60 * 1000) {
      return { text: 'Today', color: 'bg-red-500' };
    } else if (eventDate.getTime() - now.getTime() < 7 * 24 * 60 * 60 * 1000) {
      return { text: 'This Week', color: 'bg-orange-500' };
    } else {
      return { text: 'Upcoming', color: 'bg-green-500' };
    }
  };

  const status = getEventStatus();

  return (
    <motion.div
      whileHover={{ y: -5 }}
      className="card-hover group"
    >
      <div className="relative">
        {/* Event Image */}
        <div className="aspect-video bg-gray-200 dark:bg-gray-700 rounded-t-xl overflow-hidden">
          {event.poster ? (
            <img
              src={event.poster}
              alt={event.title}
              className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
            />
          ) : (
            <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-primary-100 to-secondary-100 dark:from-primary-900/20 dark:to-secondary-900/20">
              <Calendar size={48} className="text-primary-500" />
            </div>
          )}
        </div>

        {/* Status Badge */}
        <div className={`absolute top-3 left-3 ${status.color} text-white text-xs px-2 py-1 rounded-full font-medium`}>
          {status.text}
        </div>

        {/* Like Button */}
        <button
          onClick={handleLike}
          className={`absolute top-3 right-3 p-2 rounded-full transition-all duration-200 ${
            isLiked 
              ? 'bg-red-500 text-white shadow-lg' 
              : 'bg-white/90 dark:bg-gray-800/90 text-gray-600 dark:text-gray-300 hover:bg-red-500 hover:text-white'
          }`}
          disabled={likeMutation.isLoading}
        >
          <Heart size={16} fill={isLiked ? 'currentColor' : 'none'} />
        </button>
      </div>

      <div className="p-6">
        {/* Event Title */}
        <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-3 line-clamp-2 group-hover:text-primary-600 transition-colors duration-200">
          {event.title}
        </h3>

        {/* Event Description */}
        <p className="text-gray-600 dark:text-gray-400 text-sm mb-4 line-clamp-2">
          {event.shortDescription}
        </p>

        {/* Event Details */}
        <div className="space-y-2 mb-4">
          <div className="flex items-center text-sm text-gray-600 dark:text-gray-400">
            <Calendar size={14} className="mr-2 flex-shrink-0" />
            <span>{formatDate(event.startDate)}</span>
          </div>
          
          <div className="flex items-center text-sm text-gray-600 dark:text-gray-400">
            <Clock size={14} className="mr-2 flex-shrink-0" />
            <span>{formatTime(event.startTime)} - {formatTime(event.endTime)}</span>
          </div>
          
          <div className="flex items-center text-sm text-gray-600 dark:text-gray-400">
            <MapPin size={14} className="mr-2 flex-shrink-0" />
            <span className="truncate">{event.venue}</span>
          </div>
          
          <div className="flex items-center text-sm text-gray-600 dark:text-gray-400">
            <Users size={14} className="mr-2 flex-shrink-0" />
            <span>
              {event.currentParticipants}
              {event.maxParticipants > 0 && ` / ${event.maxParticipants}`} participants
            </span>
          </div>
        </div>

        {/* Event Category */}
        <div className="mb-4">
          <span className="badge-secondary">
            {event.category}
          </span>
        </div>

        {/* Action Buttons */}
        <div className="flex items-center justify-between">
          {event._id ? (
            <Link
              to={`/events/${event._id}`}
              className="btn-primary flex-1 mr-3"
            >
              <Eye size={16} className="mr-2" />
              View Details
            </Link>
          ) : (
            <span className="text-gray-400 flex-1 mr-3 cursor-not-allowed">No Details</span>
          )}
          <div className="flex items-center space-x-1 text-sm text-gray-500 dark:text-gray-400">
            <Heart size={14} className={isLiked ? 'text-red-500' : ''} />
            <span>{event.likeCount || 0}</span>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default EventCard;
