import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { Helmet } from 'react-helmet-async';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';
import { toast } from 'react-hot-toast';
import LoadingSpinner from '../components/ui/LoadingSpinner';
import { Link } from 'react-router-dom';

const MyRegistrations = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedStatus, setSelectedStatus] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);

  const itemsPerPage = 10;

  // Build query parameters
  const queryParams = new URLSearchParams({
    page: currentPage,
    limit: itemsPerPage,
    search: searchTerm,
    status: selectedStatus === 'all' ? '' : selectedStatus
  });

  // Fetch user registrations
  const { data: registrationsData, isLoading, error } = useQuery(
    ['myRegistrations', queryParams.toString()],
    () => api.get(`/api/registrations/my?${queryParams.toString()}`).then(res => res.data),
    {
      keepPreviousData: true,
      staleTime: 2 * 60 * 1000, // 2 minutes
    }
  );

  // Cancel registration mutation
  const cancelRegistrationMutation = useMutation(
    (registrationId) => api.delete(`/api/registrations/${registrationId}`),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['myRegistrations']);
        toast.success('Registration cancelled successfully');
      },
      onError: (error) => {
        toast.error(error.response?.data?.message || 'Failed to cancel registration');
      }
    }
  );

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1);
  };

  const handleFilterChange = (filterType, value) => {
    setCurrentPage(1);
    switch (filterType) {
      case 'status':
        setSelectedStatus(value);
        break;
      default:
        break;
    }
  };

  const handleCancelRegistration = (registrationId) => {
    if (window.confirm('Are you sure you want to cancel this registration?')) {
      cancelRegistrationMutation.mutate(registrationId);
    }
  };

  const clearFilters = () => {
    setSearchTerm('');
    setSelectedStatus('all');
    setCurrentPage(1);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'confirmed': return 'text-green-600 bg-green-100 dark:bg-green-900 dark:text-green-300';
      case 'pending': return 'text-yellow-600 bg-yellow-100 dark:bg-yellow-900 dark:text-yellow-300';
      case 'cancelled': return 'text-red-600 bg-red-100 dark:bg-red-900 dark:text-red-300';
      case 'waitlist': return 'text-blue-600 bg-blue-100 dark:bg-blue-900 dark:text-blue-300';
      default: return 'text-gray-600 bg-gray-100 dark:bg-gray-700 dark:text-gray-300';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'confirmed': return 'Confirmed';
      case 'pending': return 'Pending';
      case 'cancelled': return 'Cancelled';
      case 'waitlist': return 'Waitlist';
      default: return 'Unknown';
    }
  };

  const totalPages = registrationsData?.totalPages || 1;
  const hasResults = registrationsData?.registrations?.length > 0;

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
            Error Loading Registrations
          </h2>
          <p className="text-gray-600 dark:text-gray-400 mb-4">
            {error.message || 'Something went wrong while loading your registrations.'}
          </p>
        </div>
      </div>
    );
  }

  return (
    <>
      <Helmet>
        <title>My Registrations - EventSphere</title>
        <meta name="description" content="View and manage your event registrations." />
      </Helmet>

      <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
        {/* Header Section */}
        <div className="bg-white dark:bg-gray-800 shadow-sm border-b border-gray-200 dark:border-gray-700">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
                My Registrations
              </h1>
              <p className="text-lg text-gray-600 dark:text-gray-400 max-w-3xl">
                Track all your event registrations, view status updates, and manage your participation.
              </p>
            </motion.div>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {/* Search and Filters */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6 mb-8">
            <div className="flex flex-col lg:flex-row gap-4">
              {/* Search Bar */}
              <div className="flex-1">
                <input
                  type="text"
                  placeholder="Search events..."
                  value={searchTerm}
                  onChange={handleSearch}
                  className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-700 dark:text-white"
                />
              </div>

              {/* Status Filter */}
              <div className="min-w-[200px]">
                <select
                  value={selectedStatus}
                  onChange={(e) => handleFilterChange('status', e.target.value)}
                  className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-700 dark:text-white"
                >
                  <option value="all">All Status</option>
                  <option value="confirmed">Confirmed</option>
                  <option value="pending">Pending</option>
                  <option value="waitlist">Waitlist</option>
                  <option value="cancelled">Cancelled</option>
                </select>
              </div>

              {/* Clear Filters */}
              {(searchTerm || selectedStatus !== 'all') && (
                <button
                  onClick={clearFilters}
                  className="px-4 py-3 text-gray-600 dark:text-gray-400 hover:text-gray-800 dark:hover:text-gray-200 transition-colors"
                >
                  Clear Filters
                </button>
              )}
            </div>
          </div>

          {/* Results Summary */}
          <div className="flex justify-between items-center mb-6">
            <div className="text-sm text-gray-600 dark:text-gray-400">
              {isLoading ? (
                'Loading registrations...'
              ) : (
                <>
                  Showing {registrationsData?.registrations?.length || 0} of {registrationsData?.total || 0} registrations
                  {searchTerm && ` for "${searchTerm}"`}
                  {selectedStatus !== 'all' && ` with status ${selectedStatus}`}
                </>
              )}
            </div>
          </div>

          {/* Registrations List */}
          {isLoading ? (
            <div className="flex justify-center py-12">
              <LoadingSpinner size="lg" />
            </div>
          ) : hasResults ? (
            <div className="space-y-4">
              {registrationsData.registrations.map((registration, index) => (
                <motion.div
                  key={registration._id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3, delay: index * 0.1 }}
                  className="bg-white dark:bg-gray-800 rounded-xl shadow-sm border border-gray-200 dark:border-gray-700 overflow-hidden"
                >
                  <div className="p-6">
                    <div className="flex flex-col lg:flex-row lg:items-center gap-4">
                      {/* Event Poster */}
                      <div className="w-20 h-20 rounded-lg overflow-hidden flex-shrink-0">
                        {registration.event?.poster ? (
                          <img
                            src={registration.event.poster}
                            alt={registration.event.title}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full bg-gradient-to-br from-primary-100 to-secondary-100 dark:from-primary-900 dark:to-secondary-900 flex items-center justify-center">
                            <span className="text-primary-600 text-2xl">📅</span>
                          </div>
                        )}
                      </div>

                      {/* Event Details */}
                      <div className="flex-1 min-w-0">
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex-1 min-w-0">
                            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-1 truncate">
                              {registration.event?.title}
                            </h3>
                            <p className="text-sm text-gray-600 dark:text-gray-400 line-clamp-2">
                              {registration.event?.shortDescription || registration.event?.description}
                            </p>
                          </div>
                          
                          {/* Status Badge */}
                          <div className="ml-4 flex-shrink-0">
                            <span className={`inline-flex items-center gap-1 px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(registration.status)}`}>
                              {getStatusText(registration.status)}
                            </span>
                          </div>
                        </div>

                        {/* Event Meta */}
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm text-gray-600 dark:text-gray-400">
                          <div className="flex items-center gap-2">
                            <span>📅</span>
                            <span>
                              {new Date(registration.event?.date).toLocaleDateString()}
                            </span>
                          </div>
                          <div className="flex items-center gap-2">
                            <span>⏰</span>
                            <span>
                              {registration.event?.startTime} - {registration.event?.endTime}
                            </span>
                          </div>
                          <div className="flex items-center gap-2">
                            <span>📍</span>
                            <span className="truncate">
                              {registration.event?.venue}
                            </span>
                          </div>
                          <div className="flex items-center gap-2">
                            <span>👥</span>
                            <span>
                              {registration.event?.currentParticipants}/{registration.event?.maxParticipants}
                            </span>
                          </div>
                        </div>

                        {/* Registration Details */}
                        <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
                          <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                            <div className="flex items-center gap-4 text-sm text-gray-600 dark:text-gray-400">
                              <span>
                                Registered on: {new Date(registration.registrationDate).toLocaleDateString()}
                              </span>
                              {registration.registrationType && (
                                <span className="px-2 py-1 bg-gray-100 dark:bg-gray-700 rounded text-xs">
                                  {registration.registrationType}
                                </span>
                              )}
                            </div>
                            
                            <div className="flex items-center gap-2">
                              <Link
                                to={`/events/${registration.event?._id}`}
                                className="btn-outline flex items-center gap-2 px-4 py-2"
                              >
                                View Event
                              </Link>
                              
                              {registration.status === 'confirmed' && !registration.event?.isCompleted && (
                                <button
                                  onClick={() => handleCancelRegistration(registration._id)}
                                  disabled={cancelRegistrationMutation.isLoading}
                                  className="btn-outline text-red-600 border-red-300 hover:bg-red-50 dark:hover:bg-red-900/20 flex items-center gap-2 px-4 py-2"
                                >
                                  Cancel
                                </button>
                              )}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </motion.div>
              ))}
            </div>
          ) : (
            /* No Results */
            <div className="text-center py-16">
              <div className="w-24 h-24 mx-auto mb-6 bg-gray-100 dark:bg-gray-800 rounded-full flex items-center justify-center">
                <span className="text-4xl">📅</span>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                No registrations found
              </h3>
              <p className="text-gray-600 dark:text-gray-400 mb-6 max-w-md mx-auto">
                {searchTerm || selectedStatus !== 'all'
                  ? 'Try adjusting your search criteria or filters to find more registrations.'
                  : 'You haven\'t registered for any events yet. Start exploring events to get started!'}
              </p>
              {(searchTerm || selectedStatus !== 'all') ? (
                <button
                  onClick={clearFilters}
                  className="btn-primary"
                >
                  Clear Filters
                </button>
              ) : (
                <Link to="/events" className="btn-primary">
                  Browse Events
                </Link>
              )}
            </div>
          )}

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="mt-12 flex justify-center">
              <nav className="flex items-center space-x-2">
                <button
                  onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                  disabled={currentPage === 1}
                  className="px-3 py-2 rounded-lg border border-gray-300 dark:border-gray-600 disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
                >
                  Previous
                </button>
                
                {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                  let pageNum;
                  if (totalPages <= 5) {
                    pageNum = i + 1;
                  } else if (currentPage <= 3) {
                    pageNum = i + 1;
                  } else if (currentPage >= totalPages - 2) {
                    pageNum = totalPages - 4 + i;
                  } else {
                    pageNum = currentPage - 2 + i;
                  }
                  
                  return (
                    <button
                      key={pageNum}
                      onClick={() => setCurrentPage(pageNum)}
                      className={`px-3 py-2 rounded-lg border transition-colors ${
                        currentPage === pageNum
                          ? 'bg-primary-600 text-white border-primary-600'
                          : 'border-gray-300 dark:border-gray-600 hover:bg-gray-50 dark:hover:bg-gray-700'
                      }`}
                    >
                      {pageNum}
                    </button>
                  );
                })}
                
                <button
                  onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
                  disabled={currentPage === totalPages}
                  className="px-3 py-2 rounded-lg border border-gray-300 dark:border-gray-600 disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
                >
                  Next
                </button>
              </nav>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default MyRegistrations;
